.mainC {
	display: flex;
	justify-content: space-between;
	align-items: center;
	position: absolute;
	top: 35%;
	left: 26%;
	z-index: 100;
	color: white;
	font-family: "Poppins", sans-serif;
	transition: all 1s;
	opacity:0;
	animation: shadow 3s ease 1s forwards;
}


@keyframes shadow {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}



@keyframes slideLeft {
	0% {
		transform: translateX(50%);
		opacity: 0;
	}

	100% {
		transform: translateX(0%);
	}
}

@-webkit-keyframes fadeIn {
	0% {
		-webkit-transform: scale(0);
		opacity: 0.0;
	}

	60% {
		-webkit-transform: scale(1.1);
	}

	80% {
		-webkit-transform: scale(0.9);
		opacity: 1;
	}

	100% {
		-webkit-transform: scale(1);
		opacity: 1;
	}
}



.content_t h2 {
	color: #fff;
	font-size: 5.0rem;
	position: absolute;
	/* transform: translate(-50%, -50%); */
}


.content_t h2:nth-child(1) {
	color: transparent;
	-webkit-text-stroke: 2px #03a9f4;
	border-top:1px solid #eee;
}

.content_t h2:nth-child(2) {
	color: #03a9f4;
	animation: animate 4s ease-in-out infinite;
}

@keyframes animate {
	0%,
	100% {
		clip-path: polygon(
			0% 45%,
			16% 44%,
			33% 50%,
			54% 60%,
			70% 61%,
			84% 59%,
			100% 52%,
			100% 100%,
			0% 100%
		);
	}

	50% {
		clip-path: polygon(
			0% 60%,
			15% 65%,
			34% 66%,
			51% 62%,
			67% 50%,
			84% 45%,
			100% 46%,
			100% 100%,
			0% 100%
		);
	}
}


/* 반응형 main */

.mMainC h2 {
	font-size: 3.0rem;
}
